/* feature section css start */
.feature-card {
  background-color: lighten($base-color, 40%);
  position: relative;
  padding: 40px;
  @include border-radius(8px);
  z-index: 1;
  height: 100%;
  @extend %transition;
  @include media(575px) {
    padding: rem(25px); }
  &:hover {
    box-shadow: 0 5px 10px rgba(#000, 0.1);
    &::before {
      top: 10px;
      left: 10px;
      width: calc(100% - 20px);
      height: calc(100% - 20px); } }
  &::before {
    position: absolute;
    content: '';
    top: 8px;
    left: 8px;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    background-color: #fff;
    @include border-radius(8px);
    z-index: -1;
    @extend %transition; }
  &__icon {
    img {
      max-height: 65px; } }
  &__content {
    margin-top: rem(25px);
    .title {
      margin-bottom: rem(10px); } } }
/* feature section css end */

/* doctor section css start */
.doctor-card {
  padding: rem(10px);
  background-color: #fff;
  @include border-radius(5px);
  box-shadow: 0 0 10px rgba(#000, 0.1);
  @extend %transition;
  &:hover {
     box-shadow: 0 5px 15px rgba(#000, 0.1); }
  &__thumb {
    img {
      width: 100%; } }
  &__content {
    padding: rem(10px);
    margin-top: rem(10px);
    position: relative;
    .bookmark-btn {
      position: absolute;
      top: -25px;
      right: 20px;
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      color: lighten($para-color , 5%);
      font-size: rem(20px);
      box-shadow: 0 0 10px rgba(#000, 0.1);
      @include border-radius(50%);
      &.active {
        color: $base-color; } } }
  .name {
    margin-bottom: rem(10px); }
  .available-time {
    margin-top: rem(10px); }
  &__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: rem(15px);
    border-top: 1px solid $border-color;
    margin-top: rem(20px); } }

.doctor-info-list {
  li + li {
    margin-top: 5px; }
  li {
    display: flex;
    font-size: rem(14px);
    margin-top: rem(5px);
    i {
      color: $base-color;
      font-size: rem(20px);
      width: 30px;
      margin-top: 3px; }
    span {
      width: calc(100% - 20px); } } }
.ratings {
  display: flex;
  align-items: center;
  i {
    color: #F9BF00; }
  span {
    font-size: rem(14px); } }
.doctor-slider {
  .slick-list {
    margin: rem(-8px); }
  .single-slide {
    margin: rem(8px); }
  .slick-arrow {
    position: absolute;
    top: 40%;
    width: 40px;
    height: 40px;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(#000, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    @include border-radius(50%);
    z-index: 1;
    &:hover {
      background-color: $base-color;
      color: #fff; }
    &.prev {
      left: -20px;
      @include media(575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @include media(575px) {
        right: 0; } } } }
/* doctor section css end */

/* doctor details section css start */
.doctor-details-header {
  padding: rem(20px);
  background-color: #fff;
  box-shadow: 0 0 10px 0 rgba(#000, 0.12);
  @include d-flex;
  align-items: center;
  .thumb {
    width: 30%;
    @include media(575px) {
      width: 100%; } }
  .content {
    width: 70%;
    padding-left: rem(25px);
    @include media(575px) {
      width: 100%;
      padding-left: 0;
      margin-top: rem(25px); } } }
.chember-single {
  padding: rem(20px);
  background-color: #fff;
  box-shadow: 0 0 5px rgba(#000, 0.15); }
/* doctor details section css end */

/* search result section css start */
.serach-area {
  background-color: $base-color-two;
  padding: 20px 0;
  .hero-search-form {
    background-color: transparent;
    &::before {
      display: none; }
    label {
      color: darken(#fff, 5%);
      margin-bottom: rem(8px); }
    .select2-container {
      .select2-selection--single {
        @include border-radius(5px);
        .select2-selection__arrow {
          right: 12px; }
        .select2-selection__rendered {
          padding-left: 20px; } } } } }

.card-view-btn-area {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin: rem(-5px) rem(-7px);
  button {
    width: 40px;
    height: 40px;
    background-color: #f1f1f1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-size: rem(20px);
    margin: rem(5px) rem(7px);
    @include border-radius(5px);
    @extend %transition;
    &.active {
      background-color: $base-color;
      color: #fff; } } }
.list-view {
  .card-view {
    width: 100% !important; }
  .doctor-card {
    @include d-flex;
    align-items: center;
    &__thumb {
      width: 30%;
      max-height: 200px;
      overflow: hidden;
      @include border-radius(5px);
      @include media(540px) {
        width: 100%; } }
    &__content {
      width: 70%;
      padding: 0 20px;
      margin-top: 0;
      @include media(540px) {
        width: 100%;
        margin-top: 20px; }
      .bookmark-btn {
        right: auto;
        left: -50px;
        top: 5px; } } } }

/* search result section css end */


/* how it work section css start */
.work-card {
  background-color: #fff;
  padding: rem(40px) rem(30px);
  @include border-radius(8px);
  &__icon {
    width: 100px;
    height: 100px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: rgba($base-color, 0.15);
    @include border-radius(50%);
    position: relative;
    img {
      max-height: 65px; }
    .step {
      position: absolute;
      top: 8px;
      right: -11px;
      width: 30px;
      height: 30px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background-color: $base-color-two;
      color: #fff;
      font-size: rem(13px);
      @include border-radius(50%); } }
  &__content {
    margin-top: rem(25px);
    .title {
      margin-bottom: rem(10px); } } }
/* how it work section css end */


/* cta section css start */
.cta-wrapper {
  padding: rem(70px) rem(50px);
  @include border-radius(8px);
  .title {
    color: #fff; }
  p {
    color: #fff; } }
/* cta section css end */

/* testimonial section css start */
.testimoninal-card {
  padding: rem(40px) rem(40px) rem(30px) rem(40px);
  background-color: #fff;
  @include border-radius(5px);
  position: relative;
  z-index: 1;
  &::after {
    @include positionTwo;
    background-color: $base-color;
    z-index: -1;
    @include border-radius(5px);
    opacity: 0.25; }
  .description {
    position: relative;
    color: #363636;
    &::before {
      position: absolute;
      top: -21px;
      left: -34px;
      content: "\f10d";
      font-family: 'Line Awesome Free';
      font-weight: 900;
      font-size: rem(56px);
      color: $base-color;
      line-height: 1; } }
  .thumb {
    width: 85px;
    height: 85px;
    display: inline-flex;
    overflow: hidden;
    @include border-radius(50%);
    img {
      width: inherit;
      height: inherit;
      @include object-fit; } }
  .name {
    font-size: rem(20px); }
  .designation {
    font-size: rem(14px); } }

.testimonial-slider {
  .slick-list {
    margin: 0 rem(-15px); }
  .single-slide {
    margin: 0 rem(15px); }
  .slick-arrow {
    position: absolute;
    top: 50%;
    margin-top: -22px;
    width: 45px;
    height: 45px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    @include border-radius(50%);
    z-index: 1;
    &.prev {
      left: -20px;
      @include media(575px) {
        left: 0; } }
    &.next {
      right: -20px;
      @include media(575px) {
        right: 0; } } } }
/* testimonial section css end */

/* blog section css start */
.post-card {
  padding: 12px;
  @include border-radius(5px);
  background-color: #fff;
  box-shadow: 0 0 10px rgba(#000, 0.15);
  @include transition(all 0.3s);
  &:hover {
    box-shadow: 0 5px 15px rgba(#000, 0.1); }
  &__thumb {
    box-shadow: 0 0 5px rgba(#000000, 0.15);
    @include border-radius(5px);
    img {
      width: 100%;
      @include border-radius(5px); } }
  &__content {
    padding: rem(10px);
    margin-top: rem(15px); }
  &__title {
    margin-top: rem(5px); } }
.post-meta {
  @include d-flex;
  margin: rem(-5px) rem(-10px);
  li {
    margin: rem(5px) rem(10px);
    a {
      color: $para-color;
      display: flex;
      align-items: center;
      &:hover {
        color: $base-color; }
      i {
        font-size: rem(18px); } } } }
.read-more {
  color: $base-color;
  text-decoration: underline;
  &:hover {
    text-decoration: underline; } }
/* blog section css end */


/* blog-details-section css start */
.blog-details__thumb {
  position: relative;
  img {
    width: 100%; }
  .post__date {
    position: absolute;
    bottom: -1px;
    right: 0;
    width: 75px;
    text-align: center;
    .date {
      font-size: 30px;
      font-weight: 700;
      color: #ffffff;
      background-color: $base-color;
      padding: 10px 5px;
      width: 100%;
      line-height: 1; }
    .month {
      background-color: #ffffff;
      text-transform: uppercase;
      padding: 4px 5px;
      width: 100%;
      line-height: 1;
      font-size: 18px; } } }
.blog-details__content {
  margin-top: 30px;
  p {
    margin-top: 20px; }
  .blog-details__title {
    font-size: 24px; }
  blockquote {
    margin-top: 30px;
    margin-bottom: 0; } }
blockquote {
  font-size: 18px;
  color: $para-color;
  font-style: italic;
  text-align: center;
  padding: 50px 60px;
  background-color: $base-color-two;
  color: #fff;
  @media (max-width: 575px) {
    padding: 30px 40px;
    font-size: 16px; } }
.blog-details__footer {
  text-align: center;
  padding: 50px 0;
  border-bottom: 1px solid #e5e5e5;
  .caption {
    font-size: 24px;
    margin-bottom: 20px; }
  .social__links {
    justify-content: center;
    @include d-flex;
    li {
      a {
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        color: $heading-color;
        &:hover {
          background-color: $base-color;
          color: #ffffff; } } } } }
.comments-area {
  margin-top: 100px;
  margin-right: 30px;
  @include border-radius(8px);
  .title {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-size: 24px; } }
.comments-list {
  .single-comment {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    &__thumb {
      width: 100px;
      height: 100px;
      overflow: hidden; }
    &__content {
      width: (calc(100% - 100px));
      padding-left: 20px;
      @media (max-width: 480px) {
        @include flexWidth(100%);
        padding-left: 0;
        margin-top: 15px; }
      .name {
        text-transform: capitalize; }
      .date {
        font-size: 14px;
        font-style: italic; }
      p {
        margin-top: 5px; } } } }
.reply-btn {
  width: 28px;
  height: 28px;
  background-color: #f7f5fb;
  color: $base-color;
  text-align: center;
  line-height: 28px;
  font-size: 13px;
  float: right;
  margin-top: -30px;
  @include border-radius(50%);
  &:hover {
    background-color: $base-color;
    color: #ffffff; }
  i {
    @include transform(rotate(180deg)); } }
.comment-form-area {
  margin-top: 70px;
  .title {
    font-size: 24px;
    margin-bottom: 20px; } }
.comment-form {
  input, textarea {
    background-color: #ffffff;
    border: 1px solid #e5e5e5;
    &:focus {
      box-shadow: 0px 3px 18px 2px rgba(46, 25, 204, 0.16);
      border-color: transparent; } } }
/* blog-details-section css end */


/* account section css start */
.account-form {
  background-color: $base-color-two;
  .title {
    font-size: rem(32px);
    color: #fff; }
  label {
    color: darken(#fff, 2%); }
  .form--control {
    background-color: transparent;
    border-color: rgba(#fff, 0.25);
    @include placeholder(darken(#fff, 10%));
    color: #fff; }
  .input-group-text {
    background-color: $base-color;
    color: #fff;
    border-color: $base-color; }
  p {
    color: #fff; } }
.box--border {
  border: 4px solid rgba($base-color, 0.55); }
.box--shadow {
  box-shadow: 0 10px 25px rgba($base-color, 0.15); }
.z-index {
  z-index: 1; }
.bottom-img {
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0.25;
  z-index: -1; }
/* account section css end */

/* contact section css start */
.single-info {
  $icon: rem(85px);
  &__icon {
    width: $icon;
    height: $icon;
    font-size: calc(#{$icon} / 2);
    background-color: $base-color-two;
    @include media(991px) {
      width: rem(75px);
      height: rem(75px); }
    @include media(380px) {
      width: rem(60px);
      height: rem(60px);
      font-size: calc(#{rem(60px)} / 2); } }
  &__content {
    width: calc(100% - #{$icon});
    padding-left: rem(25px);
    .title {
      font-size: rem(28px);
      @include media(991px) {
        font-size: rem(24px); }
      @include media(380px) {
        font-size: rem(22px); } } } }
.contact-info-list {
  .single-info {
    padding: rem(20px) 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; } } }
.contact-form {
  background-color: $base-color-two;
  .title {
    font-size: rem(32px);
    color: #fff; }
  label {
    color: darken(#fff, 2%); }
  .form--control {
    background-color: transparent;
    border-color: rgba(#fff, 0.25);
    @include placeholder(darken(#fff, 10%));
    color: #fff; }
  .input-group-text {
    background-color: $base-color;
    color: #fff;
    border-color: $base-color; }
  p {
    color: #fff; }
  select {
    background-color: transparent;
    color: #fff;
    border-color: rgba(#fff, 0.25);
    option {
      background-color: $base-color; } } }
/* contact section css end */
