/* hero section css start */
.hero {
  padding-top: rem(180px);
  padding-bottom: rem(100px);
  background-position-y: top;
  position: relative;
  z-index: 1;
  @include media(1199px) {
    padding-top: rem(120px); }
  @include media(575px) {
    padding-top: rem(100px); }
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.55;
    z-index: -1; }
  &__title {
    font-size: rem(72px);
    line-height: 1.1;
    font-weight: 700;
    color: #fff;
    @include media(1399px) {
      font-size: rem(66px); }
    @include media(1199px) {
      font-size: rem(56px); }
    @include media(991px) {
      font-size: rem(48px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__description {
    color: #fff; } }

.hero-search-wrapper {
  margin-top: rem(70px);
  padding: 0 20px; }
.hero-search-form {
  background-color: #fff;
  padding: rem(15px);
  @include border-radius(8px);
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: -10px;
    left: -10px;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: #fff;
    opacity: 0.5;
    @include border-radius(8px);
    z-index: -1; }
  label {
    margin-bottom: 0; }
  .select2-container .select2-selection--single {
    border: none;
    border-bottom: 1px solid $border-color;
    @include border-radius(0);
    .select2-selection__rendered {
      padding-left: 0; }
    .select2-selection__arrow b {
      left: 85%; } } }
.category-slider {
  margin-top: 35px;
  .slick-list {
    margin: 0 rem(-7px); }
  .single-slide {
    padding: 0 rem(7px); }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 35px;
    height: 35px;
    margin-top: -17px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50%);
    z-index: 1;
    @include media(575px) {
      background-color: $base-color;
      color: #fff; }
    &.prev {
      left: -17px;
      @include media(575px) {
        left: -5px; } }
    &.next {
      right: -17px;
      @include media(575px) {
        right: -5px; } } } }

.category-card {
  background-color: #fff;
  padding: rem(20px);
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include border-radius(5px);
  text-align: center;
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    width: 50px;
    height: 50px;
    background-color: rgba($base-color, 0.15);
    @include border-radius(50%);
    @include transform(translate(-50%, -50%));
    z-index: -1;
    @extend %transition;
    @include border-radius(5px);
    opacity: 0; }
  &:hover {
    &::before {
      width: 100%;
      height: 100%;
      opacity: 1; } }

  img {
    display: inline-block;
    height: 65px; }
  .title {
    font-size: rem(14px);
    margin-top: rem(15px);
    width: 100%; } }
/* hero section css end */


/* inner-page-hero css start */
.inner-page-hero {
  padding-top: 70px;
  padding-bottom: 70px;
  position: relative;
  z-index: 1;
  background-position-y: center;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.85;
    z-index: -1; }
  .page-title {
    font-weight: 600;
    color: #ffffff; }
  .page-list {
    @include d-flex;
    margin-top: 15px;
    li {
      padding: 0 15px;
      position: relative;
      color: rgba(#ffffff, 0.7);
      &:first-child {
        padding-left: 0;
        &::before {
          display: none; } }
      &::before {
        position: absolute;
        content: '/';
        color: #ffffff;
        left: 0px; }
      a {
        color: #ffffff; } } } }
/* inner-page-hero css end */
